import React from "react";
import resume from './Seth_Holland_Resume_2022.pdf'

const AboutFour = () => {
  return (
    <>
      <div className="shane_tm_section" id="about">
        <div className="shane_tm_about">
          <div className="container">
            <div className="about_inner">
              <div className="left">
                <div className="image">
                  <img src="/img/placeholders/1-1.jpg" alt="placeholder" />
                  <div
                    className="main"
                    style={{
                      backgroundImage: `url(${
                        process.env.PUBLIC_URL + "img/about/4.jpg"
                      })`,
                    }}
                  ></div>
                </div>
                {/* End image */}
              </div>
              {/* End left */}

              <div className="right">
                <div className="shane_tm_title">
                  <span>About Me</span>
                  <h3>Full Stack Software Engineer</h3>
                </div>
                <div className="text">
                  <p>
                   Self-motivated, friendly, and solution-oriented Software Engineer with over 14 years of experience
                   in customer-facing roles involving custom product development and sales. Customer-focused team player
                   offering expertise in customer relations, technical knowledge, and project management. Passionate about
                   continuous learning with a teaching mindset. Currently developing project consisting of JavaScript, Python,
                   React, Redux, PostgreSQL, Flask, Express, SQLAlchemy, Sequelize, HTML5, and CSS3.
                  </p>
                </div>
                <div className="shane_tm_button">
                  <a href={resume} target='_blank' rel="noreferrer">
                    View Resume
                  </a>
                </div>
              </div>
              {/* End right */}
            </div>
          </div>
          {/* End container */}
        </div>
      </div>
    </>
  );
};

export default AboutFour;
