import React from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import SimpleReactLightbox from "simple-react-lightbox";
import { SRLWrapper } from "simple-react-lightbox";

const tabList = ["All"];

const tabListContent = [
  {
    porftoliItems: [
      {
        img: "1",
        live: " Live Site",
        github: "GitHub",
        portfolioLink: "https://angtft-sh.herokuapp.com/",
        portfolioGitHub: "https://github.com/LaterBlackBird/Aint_nobody_got_time_for_that",
        description: "A meal planning application with a simple and intuitive design built with JavaScript, Node.js, React, Redux, Python, Flask, SQLAlchemy, and PostgreSQL",
      },
      {
        img: "2",
        live: "Live Site",
        github: "GitHub",
        portfolioLink: "https://harmony-aug21.herokuapp.com/",
        portfolioGitHub: "https://github.com/LaterBlackBird/Harmony",
        description: "A real-time communication application modeled after Discord.com built with JavaScript, Node.js, React, Redux, Python, Flask, SQLAlchemy, and PostgreSQL",
      },
      {
        img: "3",
        live: "Live Site",
        github: "GitHub",
        portfolioLink: "https://roasted-sh.herokuapp.com/",
        portfolioGitHub: "https://github.com/LaterBlackBird/roasted",
        description: "A social application centered around people's love for coffee built with JavaScript, React, Redux, PostgreSQL, Express, and Sequelize",
      },
    ],
  },
]




const Portfolio = () => {
  return (
    <div className="shane_tm_section" id="portfolio">
      <SimpleReactLightbox>
        <div className="shane_tm_portfolio">
          <div className="container">
            <div className="positon-relative">
              <div className="shane_tm_title">
                <div className="title_flex">
                  <div className="left">
                    <span>Portfolio</span>
                    <h3>Full Stack Projects</h3>
                  </div>
                </div>
              </div>
              {/* End shane_tm_title */}

              <div className="portfolio_filter">
                <Tabs>
                  <TabList>
                    {tabList.map((val, i) => (
                      <Tab key={i}>{val}</Tab>
                    ))}
                  </TabList>
                  {/* End tablist */}

                  <div className="portfolio_list has-effect">
                    <SRLWrapper>
                      {tabListContent.map((tabContent, i) => (
                        <TabPanel key={i}>
                          <ul className="gallery_zoom">
                            {tabContent.porftoliItems.map((item, i) => (
                              <li key={i}>
                                <div className="inner">
                                  <div className="entry shane_tm_portfolio_animation_wrap">
                                    <a href={`/img/portfolio/${item.img}.jpg`}>
                                      <img
                                        src={`/img/portfolio/${item.img}.jpg`}
                                        alt={item.description}
                                      />
                                    </a>
                                  </div>
                                  <div className="mobile_title">
                                    <h3>
                                      <a
                                        href={item.portfolioLink}
                                        rel="noopener noreferrer"
                                        target="_blank"
                                      >
                                        {item.live}
                                      </a>
                                    </h3>
                                    <h3>
                                      <a
                                        href={item.portfolioGitHub}
                                        rel="noopener noreferrer"
                                        target="_blank"
                                      >
                                        {item.github}
                                      </a>
                                    </h3>
                                  </div>
                                </div>
                              </li>
                            ))}
                          </ul>
                        </TabPanel>
                      ))}
                    </SRLWrapper>
                    {/* End tabpanel */}
                  </div>
                  {/* End list wrapper */}
                </Tabs>
              </div>
            </div>
          </div>
        </div>
      </SimpleReactLightbox>
    </div>
  );
};

export default Portfolio;
